<script>
import { Toast, List, Button, Icon, Tag ,NavBar} from "vant";

export default {
  components: {
    [List.name]: List,
    [Button.name]: Button,
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [NavBar.name]:NavBar
  },

  data() {
    return {
      loading: false,
      finished: false,
      active: 0,
      page: 1,
      pageSize: 20,
      id:"",
      customerInfo:{},
      logList:[],
      typeList:[]
    };
  },
    filters: {
      
      getTypeName_Filter(value1,typeList){
          let currType =  typeList.filter(item=>{
               return item.id == value1;
           });
           if(currType.length>0){
               return currType[0].type_desc;
           }
         return "";
      },
      taxName_Filter(value){
        if(value == 1){
           return "小规模";
        }else if(value == 2){
            return "一般纳税人";
        }else {
             return "未定";
        }
      },
      company_Name_Filter(value){
        if(value == 1){
           return "内资";
        }else if(value == 2){
            return "外资";
        }else if(value == 3){
            return "离岸公司";
        }else {
            return "未定";
        } 
      }
  },
  created() {
    let that = this;
    this.id = this.$route.query.id
    this.get_type_data().then(result=>{
        that.typeList = result;
        this.findSerachList();
    })
   
    
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    selectActon(value) {
      this.finished = false;
      this.list = [];
      //  this.onLoad();
    },
    
    get_type_data(){
        let that = this;
        return new Promise(function(resolve){
            that.$http
                .post("/admin/business/get_type", {})
                .then(function (response) {
                // eslint-disable-next-line no-console
                     console.log(response.data.data.source);
                    if (response.data.code === 2000) {
                       // 数据全部加载完成purpose  source
                        if(response.data.data){
                            resolve(response.data.data.source || []);
                        }else{
                            resolve([]);
                        }
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .catch(function (error) {
                     resolve([]);
                });
        });
    },
    findSerachList() {
      let that = this;
      this.$http
        .post("/admin/business/follow_log", {
            customer_id:this.id
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            //   // 数据全部加载完成purpose  source
             if(response.data.data){
                that.customerInfo = response.data.data.info;
                that.logList      = response.data.data.list;
             }
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {});
    },
    onLoad() {
